import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import imagesLoaded from 'vue-images-loaded'

import axios from '@/api/axiosApi'
import { isIphoneX } from '@/assets/js/brower'

export default {
  data() {
    return {
      title: '内容详情页',
      contentHTML: '',
      contentText: ''
    }
  },
  directives: {
    imagesLoaded
  },
  components: {
    VHeader, Scroll
  },
  created() {
    this.scrollHeight = window.innerHeight - (40 + 20)
    if (isIphoneX()) {
      this.scrollHeight = window.innerHeight - (40 + 34)
    }

    this.title = this.$route.params.title
    this._getDetail()
  },
  methods: {
    _getDetail() {
      axios.get(this, `/v1/contentDesc/${this.$route.params.contentId}`, null, (data) => {
        this.contentHTML = data.contentHtml
        this.$vux.loading.show({
          text: '加载中...'
        })
      })
    },
    imageLoaded(instance) {
      // 处理加载图片时不可滚动
      this.$vux.loading.hide()
      this.$refs.scroll.scroll.refresh()
    }
  }
}



